<template>
  <div id="app">
    <h3>{{ title }}</h3>

    <form class="form_container">
      <div class="form-title">
        <label for="text">Note Title</label>
        <input type="text" v-model="note.title" />
      </div>

      <div class="form-text">
        <label for="text">Note Text</label>
        <textarea v-model="note.text"></textarea>
        <small v-if="show">Empty Note</small>
      </div>
      <button @click.prevent="addNotes">Submit</button>
    </form>

    <div class="card-container">
      <div
        class="card"
        v-for="(note, index) in notes"
        :key="index"
        @dblclick="removeNote"
      >
        <h4>{{ note.title }}</h4>
        <small>{{ note.date }}</small>
        <p>{{ note.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: 'NoteMaster',
      note: {
        title: '',
        text: ''
      },
      notes: [],
      show: false
    }
  },
  methods: {
    addNotes () {
      const { title, text } = this.note
      if (this.note.title === '') {
        this.show = true
        return
      } else {
        this.notes.push({
          title,
          text,
          date: new Date(Date.now()).toLocaleString()
        })
        this.show = false
      }
      this.note.title = ''
      this.note.text = ''
    },
    removeNote (index) {
      this.notes.splice(index, 1)
    }
  }
}
</script>

